.price-book-divide h2{
    font-size:20px;
    font-weight:400;
    color:#4db6ac;
}
.price-book-divide{
    margin-bottom:10px;
}
.row{
    margin-right:0;
    margin-right:0px;
}

.menu-items {
    max-width: 100vw;
}

.item1 {
    margin:1rem;
}

.flex-box {
    display: flex;
    justify-items: space-between;
    flex-wrap: wrap;

}

.btn-primary{
    background-color: #4db6ac;
    outline:none;
    border:none;
    transition:all 0.3snease-in-out;
}

.btn-primary:hover{
    background-color: #6ebeb5;
    outline:none;
    border:none;
    
}
.btn-warning:hover{
    background-color:rgb(65, 107, 100);
    outline:none;
    border:none;
    
}
.img-fluid{
    height:220px;
}
.main-title h1{
    font-size: 25px;
    font-weight: bold;
    text-transform: uppercase;
    
    
}
.heading{
    text-align: center;
}
.btn{
    margin:15px;
    background-color: #b9e60044;
    border-radius: 1rem;
}